import appTabNav from "../../../components/basic-component/app-tab-nav/app-tab-nav.vue";

import { mapState } from "vuex";

export default {
    data() {
        return {
            tabList: [
                {id:1, name: '出售中'},
                {id:0, name: '下架中'}
            ],
            theme: {
                color: '#446dfd'
            },
            activeTab: '1',
            getFocus: false,
            keyword: '',
            page: 1,
            id: null,
            more_list: false,
            iphone_x: false,
            status: null,
            page_loading: true,
            list: [],
            is_switch: false,
            is_delete: false,
            toSearch: false,
            device: null,
            first: false,
            loading: false
        }
    },
    components: {
        "app-tab-nav": appTabNav
    },
    computed: {
        ...mapState({
            userInfo: state => state.user.info,
            appImg: state => state.mallConfig.__wxapp_img.mall,
            appSetting: state => state.mallConfig.mall.setting,
            adminImg: state => state.mallConfig.__wxapp_img.app_admin,
        })
    },
    methods: {
        toEdit(id) {
            uni.navigateTo({
                url: '/pages/app_admin/add-goods/add-goods?id=' + id
            })
        },
        clearSearch() {
            this.keyword = '';
            this.reload();
        },
        toAdd() {
            uni.navigateTo({
                url: '/pages/app_admin/add-goods/add-goods'
            })
        },
        toRedirect(url) {
            uni.redirectTo({
                url: url
            })
        },
        beSearch() {
            this.getFocus=false;
            if (this.keyword.length == 0) {
                this.toSearch = !this.toSearch
            }else {
                this.toSearch = this.toSearch;
            }
        },
        reload() {
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            this.list = [];
            this.page = 1;
            this.id = null;
            this.getList();
        },
        cancel() {
            this.id = null;
            this.is_delete = false;
            this.is_switch = false;
        },
        toDelete(e) {
            this.is_delete = true;
        },
        showMenu(id) {
            if(this.id == id) {
                this.id = null
            }else {
                this.id = id;
            }
        },
        toSwitch(status,id) {
            let that = this;
            that.id = id;
            that.status = status;
            that.is_switch = true;
        },
        tabStatus(e) {
            if(this.loading) {
                return false
            }
            this.list = [];
            this.page = 1;
            this.activeTab = e.currentTarget.dataset.id;
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            this.getList();
        },
        getList() {
            let that = this;
            if(that.loading) {
                return false
            }
            that.loading = true;
            let para = {
                keyword: that.keyword
            };
            para.status = that.activeTab;
            that.$request({
                url: that.$api.app_admin.goods,
                data: {
                    page: that.page,
                    search: JSON.stringify(para)
                }
            }).then(response=>{
                that.loading = false;
                uni.hideLoading();
                that.$hideLoading();
                if(response.code === 0) {
                    that.list = that.list.concat(response.data.list);
                    that.page++;
                    that.more_list = false;
                    that.first = true;
                    if (response.data.list.length == response.data.pagination.pageSize) {
                        that.more_list = true;
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.loading = false;
                uni.hideLoading();
                that.$hideLoading();
            });
        },
        goods_destroy() {
            let that = this;
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            that.$request({
                url: that.$api.app_admin.goods_destroy,
                data: {
                    id: that.id
                },
                method: 'post'
            }).then(response=>{
                uni.hideLoading();
                if(response.code == 0) {
                    that.page = 1;
                    that.list = [];
                    that.id = null;
                    that.is_delete = false;
                    that.getList();
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                uni.hideLoading();
            });
        },

        goods_switch() {
            let that = this;
            that.$request({
                url: that.$api.app_admin.goods_switch,
                data: {
                    status: that.status,
                    id: that.id
                },
                method: 'post'
            }).then(response=>{
                if(response.code == 0) {
                    that.page = 1;
                    that.id = null;
                    that.list = [];
                    that.is_switch = false;
                    that.getList();
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
            });
        },
    },

    onLoad() { this.$commonLoad.onload();
        let that = this;
        uni.getSystemInfo({
            success: function (res) {
                if(res.model.indexOf('iPhone X') > -1 || res.model.indexOf('iPhone 11') > -1 || res.model.indexOf('iPhone11') > -1 || res.model.indexOf('iPhone12') > -1 || res.model.indexOf('Unknown Device') > -1) {
                    that.iphone_x = true;
                }
            }
        })
        this.activeTab = '1';
        this.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        this.getList();
    },

    onReachBottom: function() {
        if (this.more_list) {
            this.getList();
        }
    },

    onShow() {
        let that = this;
        that.id = null;
        if(that.first) {
            this.$showLoading({
                type: 'global',
                text: '加载中...'
            });
            that.list = [];
            that.page = 1;
            that.getList();          
        }
    }
}
